<template>
  <div>
    <div class="">
      <a-button class="editable-add-btn" icon="plus" @click="addItemAuto">
        {{ $t('list.ad') }}
      </a-button>
      <a-button type="danger" class="editable-add-btn" icon="close" @click="handleClear">
        {{ $t('list.cl') }}
      </a-button>
    </div>

    <a-table
        :bordered="true"
        :data-source="dataSource"
        :columns="JSON.parse($t('list.col'))"
        :scroll="{ x: 'calc(900px + 50%)'}"
        v-if="!inResize"
    >
      <template slot="title" slot-scope="text, record">
        <editable-cell :text="text" @change="onCellChange(record.key, 'title', $event)"/>
      </template>
      <template slot="subtitle" slot-scope="text, record">
        <editable-cell :text="text" @change="onCellChange(record.key, 'subtitle', $event)"/>
      </template>
      <template slot="url" slot-scope="text, record">
        <editable-cell :text="text" @change="onCellChange(record.key, 'url', $event)"/>
      </template>
      <template slot="remark" slot-scope="text, record">
        <editable-cell :text="text" @change="onCellChange(record.key, 'remark', $event)"/>
      </template>
      <template slot="operation" slot-scope="text, record">
        <a-popconfirm
            v-if="dataSource.length"
            :title="$t('list.dela')"
            @confirm="() => onDelete(record.key)"
        >
          <a href="javascript:;">{{ $t('list.del') }}</a>
        </a-popconfirm>
      </template>
    </a-table>
  </div>
</template>

<script>
import EditableCell from "@/components/list/editableCell";
import {Button, Table, Popconfirm} from 'ant-design-vue'
import { createNamespacedHelpers } from 'vuex'
const { mapMutations } = createNamespacedHelpers('luckCardModule')
import Vue from 'vue'

Vue.use(Table)
Vue.use(Button)
export default {
  name: 'item-list',
  props: {
    /*    dataSource: {
          type: Array,
          // default: function () { return [] }
          default: () => []
        }*/
  },
  components: {
    EditableCell
  },
  data() {
    return {
      inResize: false
    };
  },
  methods: {
    refresh () {
      // remark: force refresh dom, fixed row height
      this.inResize = true
      Vue.nextTick(t => {
        typeof (t)
        this.inResize = false
      })
    },
    onCellChange(key, dataIndex, value) {
      const dataSource = [...this.dataSource];
      const target = dataSource.find(item => item.key === key);
      if (target) {
        target[dataIndex] = value;
        this.dataSource = dataSource;
      }
    },
    onDelete(key) {
      this.delItemByKey(key)
    },
    handleClear () {
      this.$confirm({
        title:  this.$t('list.clTitle'),
        content: this.$t('list.content'),
        okText: this.$t('list.okText'),
        okType: 'danger',
        cancelText: this.$t('list.cancelText'),
        onOk: () => {
          this.cleatAll()
        },
        onCancel () {
          console.log('Cancel')
        },
      })
    },
    ...mapMutations(['addItemAuto']),
    ...mapMutations(['delItemByKey']),
    ...mapMutations(['cleatAll'])
  },
  created() {
    this.refresh()
  },
  mounted() {
  },
  computed: {
    dataSource() {
      return this.$store.getters['luckCardModule/getDataSource']
    }
  }
};

Vue.use(Popconfirm)
</script>

<style>

.ant-table-title {
  visibility: hidden;
  height: 0;
}

.editable-add-btn {
  /*width: 100px;*/
  margin-right: 10px;
}

</style>
